@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --blue: #00bdee;
  --yellow: #cefe0e;
  --greyblue: #002d38;
  --darkgrey: #0d1d22;
  --black: #000000;
  --white: #ffffff;
  --lightgrey: #f6f7f8;
  --gradient: linear-gradient(
    130deg,
    hsl(192deg 100% 47%) 9%,
    hsl(187deg 100% 46%) 39%,
    hsl(178deg 100% 43%) 48%,
    hsl(165deg 100% 46%) 54%,
    hsl(133deg 82% 66%) 60%,
    hsl(84deg 87% 62%) 68%,
    hsl(57deg 96% 51%) 93%
  );
  --gradient2: linear-gradient(90deg, #18191a, #00bdee);
}

* {
  margin: 0;
  list-style: none;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section h1 {
  max-width: 500px;
  font-size: 6rem;
  line-height: 5rem;
  color: var(--darkgrey);
  margin: 2rem;
  text-align: end;
  text-transform: uppercase;
}

.section h1 em {
  all: unset;
  background: url(../public/calc-bg4.svg);
  background-position: 60% 50%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}


.section p {
  margin: 2rem;
  max-width: 500px;
}

.section {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 400;
  text-align: start;
}

#logo {
  height: 6rem;
  margin: 1rem;
  grid-column-start: 1;
  grid-column-end: 2;
}

.bullet-container {
  display: flex;
  justify-content: space-between;
  margin: 2rem;
  gap: 2rem;
}

.bullet-container div {
  flex: 1 1 0;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 2rem;
  max-width: 300px;
}

.flex-direction-override {
  flex-direction: column;
}

.testimonial-img {
  border-radius: 50%;
  height: 120px;
  padding: 1rem;
  width: auto;
}

.quote-container {
  display: flex;
  flex-direction: column;
}

.quote-container h5 {
  color: rgba(0, 0, 0, 0.8);
}

.testimonial-card {
  background-color: rgb(33, 33, 33);
  min-width: 300px;
  max-width: 300px;
  height: 500px;
  border-radius: 3rem;
  box-sizing: border-box;
  padding: 3rem;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 0;
  right: -500px;
  animation: carousel 40s linear 0s;
}

.testimonial-card:hover {
  background: linear-gradient(to bottom right, #06182b, #252618);
}

#carousel {
  position: relative;
  width: 100vw;
  height: calc(500px + 2rem);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 400;
  text-align: start;
  color: white;
  background-color: var(--lightgrey);
}

@keyframes carousel {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-2400px);
  }
}

.testimonial-card em {
  font-weight: 800;
  background: var(--gradient);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.dark {
  background-color: var(--lightgrey);
  color: var(--darkgrey);
}

.light {
  background-color: var(--white);
  color: var(--darkgrey);
}

.large {
  min-height: 600px;
}

.medium {
  min-height: 400px;
}

.small {
  min-height: 250px;
}

#graph {
  display: flex;
  align-items: center;
  gap: 2rem;
}

#graph svg {
  height: 150px;
  width: auto;
  transition: all 0.3s;
}

#content-graph {
  display: flex;
  align-items: center;
  justify-content: center;
}

#content-graph svg {
  width: 50vw;
}

.CTA-button {
  all: unset;
  cursor: pointer;
  background-color: var(--yellow);
  padding: 1rem;
  border-radius: 50px;
  color: var(--greyblue);
  height: fit-content;
  align-self: center;
}

.CTA-button:hover {
  color: var(--blue);
}

.question-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  animation: fadein 1s ease-in-out 0.3s both;
}

.question-input {
  display: flex;
  width: 100%;
  padding: 2rem;
}

.question-text {
  font-size: 0.8rem;
  width: 100%;
}

.question-options {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding-left: 0;
  animation: fadein 1s ease-in-out 0.6s both;
}

.question-option {
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  margin: 0.5rem;
  border-radius: 50px;
  background: var(--white);
  background-color: rgba(255, 255, 255, 1);
  background-blend-mode: overlay;
  color: var(--darkgrey);
  font-weight: 600;
  height: fit-content;
  width: 150px;
}
.question-option:hover {
  background-blend-mode: lighten;
}

.calculator-wrapper {
  display: flex;
  justify-content: space-between;
  background-image: url(../public/calc-bg4.svg);
  background-position: 50% 60%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #111111;
  align-items: center;
  width: 100vw;
  height: auto;
  position: fixed;
  top: 0;
  z-index: 2;
  transition: all 1s;
  color: var(--white);
  box-sizing: border-box;
  box-shadow: 0 50px 100px -20px rgb(50 50 93 / 25%),
    0 30px 60px -30px rgb(0 0 0 / 30%);
}

#calculator-wrapper h1 {
  font-size: 2rem;
}

#calc-nav {
  display: none;
  gap: 0.5rem;
  height: 100%;
  margin: 1rem 1rem;
  grid-column-start: 2;
  grid-column-end: 3;
  justify-self: end;
  height: 100%;
}

.calc-container {
  position: relative;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 8rem auto 8rem;
  margin: 1rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.nav-button,
.min-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0px;
  width: 0px;
  padding: 1.1rem;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 300;
  transition: all 0.5s;
}

.nav-button:hover,
.min-button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

#calculator-wrapper:hover #graph svg {
  height: 250px;
  width: auto;
}
#calculator-wrapper ul > li,
button {
  cursor: pointer;
}

#results-and-submit-container {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}

#weight-select-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

#weight {
  display: flex;
  flex-direction: column;
}

#weightType {
  margin: 1rem;
  border-radius: 1rem;
  padding: 0.25rem;
}

#weightValue {
  margin: 1rem;
  border-radius: 1rem;
  border: none;
  padding: 0.25rem;
}

#data-summary {
  display: flex;
  flex-direction: column;
}

#form-wrapper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: fit-content;
  justify-content: center;
  align-items: center;
  margin: 2rem;
}
#form-wrapper form {
  display: flex;
  flex-direction: column;
}

#progress-bar {
  width: calc(100% - 4.5rem);
  max-width: 100%;
  margin: auto;
  height: 0.4rem;
  border: solid 1px var(--white);
  border-radius: 5px;
  grid-column-start: 1;
  grid-column-end: 3;
  animation: fadein 1s ease-in-out 0.6s both;
  transition: all 1s ease-in-out;
  overflow: hidden;
  position: absolute;
  top: calc(100% - 3.75rem);
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

#progress {
  width: 0%;
  height: 20px;
  background: var(--yellow);
  transition: all 0.8s ease-in;
}

/*calculator minimise test*/

.nav-active {
  margin: 5rem;
  width: calc(100vw - 10rem);
  height: calc(100vh - 10rem);
  border-radius: 50px;
  max-width: 900px;
  min-height: 500px;
}

.minimised {
  width: calc(100vw - 2rem);
  height: 10rem;
  overflow: hidden;
  margin: 1rem;
  min-height: 0;
}

/*animated graph*/

#monday,
#tuesday,
#wednesday,
#thursday,
#friday,
#saturday,
#sunday {
  transform-origin: 0% 3%;
  scale: 1 1.2;
  animation-name: scalebar;
  animation-duration: 1.5s;
  animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
#tuesday {
  animation-delay: 0.5s;
}

#wednesday {
  animation-delay: 0.24s;
}
#thursday {
  animation-delay: 0.65s;
}
#friday {
  animation-delay: 0.89s;
}
#saturday {
  animation-delay: 1.5s;
}
.stop1 {
  stop-color: var(--yellow);
}

.stop2 {
  stop-color: rgb(56, 180, 128);
}

.stop3 {
  stop-color: var(--blue);
}
@keyframes scalebar {
  from {
    scale: 1 1.2;
  }

  to {
    scale: 1 0.8;
  }
}

/* mobile */

@media screen and (max-width: 850px) {
  .section {
    flex-direction: column;
  }
  .section h1 {
    font-size: 5rem;
    line-height: 4rem;
  }

  .bullet-container {
    flex-direction: column;
  }

  .nav-active {
    margin: 1rem;
    width: calc(100vw - 1rem);
    height: calc(100vh - 2rem);
    border-radius: 50px;
  }

  .question-wrapper {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .question-options {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

/* section animation */

.section > * {
  overflow: hidden;
  transform: translateY(100px);
  opacity: 0;
}

.section > .active {
  transform: translateY(0px);
  opacity: 100;
  transition: transform 0.5s ease-out, opacity 0.8s ease-in;
}

/* question fade in animation */

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
